
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ICategoryForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import CategoryForm from '@/views/catalog/category/forms/CategoryForm.vue'

export interface IData {
  model: ICategoryForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [loadForm, loadStatus, loadError] = useFetching((id: number) => {
      return store.dispatch('category/loadForm', id)
    }) as ResponseFetching<Promise<ICategoryForm>>

    const [saveForm, saveStatus, saveError] = useFetching((id: number, dataForm: ICategoryForm) => {
      return store.dispatch('category/updateForm', { id, dataForm })
    }) as ResponseFetching<Promise<ICategoryForm>>

    return {
      loadForm,
      loadStatus,
      loadError,
      saveForm,
      saveStatus,
      saveError
    }
  },

  components: {
    CategoryForm,
    PageCard
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending || this.loadStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: null
    }
  },

  async mounted () {
    this.model = await this.loadForm(this.$route.params.id)
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'category' })
    },

    async handleSubmit () {
      const data: ICategoryForm = await this.saveForm(this.$route.params.id, this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }
    }
  }
})
